  padding: 4vh 0vh;
  color: #324659;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-size: 10vh;
  letter-spacing: 1px;
}

nav ul {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 3vh;
