@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");
.Home {
  color: black;
}

.Home .HomeHeader {
  position: relative;
  height: 100vh;
  width: 100%;
  background: url("../assets/kitchenMain.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  backdrop-filter: black;
}

.Home .HomeHeader h1 {
  position: relative;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
  font-family: "Rubik", sans-serif;
  font-size: 5rem;
  font-weight: 900;
  letter-spacing: 1px;
  color: white;
  top: 50%;
  transform: translateY(-50%);
  animation: headerFadeIn 1s;
}

.Home .HomeHeader h2 {
  position: relative;
  width: 60%;
  margin: 0 auto;
  margin-top: 3vh;
  font-family: "Raleway", sans-serif;
  font-size: 2rem;
  font-weight: 300;
  letter-spacing: 0.5px;
  color: white;
  top: 50%;
  transform: translateY(-50%);
  animation: headerFadeIn 1s;
}

@keyframes headerFadeIn {
  0% {
    opacity: 0;
    transform: translate(0, -100%);
  }
  100% {
    opacity: 1;
    transform: translate(0, -50%);
  }
}

.Home .HomeDescription {
  max-width: 80vh;
  margin: 0 auto;
  padding-top: 15vh;
  text-align: left;
}
.Home .HomeDescription h1 {
  margin-top: 10vh;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 3rem;
  font-weight: 100;
  font-family: "Roboto Condensed", sans-serif;
  animation: fadeInLeft 2s;
}
.Home .HomeDescription h2 {
  margin-top: 10vh;
  color: rgb(66, 66, 66);
  font-weight: 100;
  font-size: 1.2rem;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 25px;
  text-align: left;
  animation: fadeInLeft 3s;
}

.Home .servicesList ul {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 50px;
  grid-row-gap: 50px;
  grid-auto-rows: 1fr;
  width: 70%;
  max-width: 130vh;
  margin: 0 auto;
  margin-top: 15vh;
  list-style-type: none;
}

.Home .servicesList ul li {
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(194, 194, 194);
  padding: 70px 15px;
  text-transform: uppercase;
  animation: fadeIn 2s;
}
.Home .servicesList ul li h1 {
  color: rgb(41, 41, 41);
  font-weight: 900;
  font-size: 1.5rem;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1px;
}

.Home .servicesList ul li p {
  cursor: pointer;
  margin-top: 30px;
  font-weight: 900;
  font-size: 1.2rem;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1px;
  color: #283b4d;
  animation: fadeInLeft 1s;
}

.Home .miniContact {
  position: relative;
  margin-top: 10vh;
  min-height: 1000px;
  background-image: radial-gradient(
      circle at top left,
      rgb(56, 53, 53) 10%,
      rgba(43, 48, 58, 0.5) 100%
    ),
    url("../assets/homeContact.jpg") !important;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.Home .miniContact .leftCorner {
  height: 20vh;
  width: 40%;
  background-color: rgb(255, 255, 255);
  clip-path: polygon(100% 0, 0 31%, 0 0);
}

.Home .miniContact .leftCorner2 {
  top: 0;
  position: absolute;
  height: 20vh;
  width: 100%;
  background-color: rgb(161, 159, 159);
  clip-path: polygon(35% 0, 75% 0%, 100% 0, 100% 10%, 37% 8%, 0 30%);
}

.Home .miniContact h1 {
  color: white;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 3.5rem;
  font-family: "Rubik", sans-serif;
  line-height: 70px;
  width: 60%;
  text-align: left;
  margin: 0 auto;
  animation: fadeInLeft 2s;
}

.Home .miniContact h2 {
  color: white;
  font-size: 1.5rem;
  font-weight: 300;
  font-family: "Raleway", sans-serif;
  padding-right: 15%;
  width: 60%;
  text-align: left;
  line-height: 40px;
  margin: 0 auto;
  margin-top: 50px;
  animation: fadeInLeft 1s;
}

.Home .miniContact .inputText {
  width: 60%;
  padding-right: 15%;
  margin: 0 auto;
  margin-top: 40px;
  text-align: left;
  display: flex;
  justify-content: space-between;
}

.Home .miniContact .inputText input[type="text"] {
  color: black;
  padding: 15px;
  font-size: 1.3rem;
  font-weight: 300;
  font-family: "Raleway", sans-serif;
  width: 48%;
}

.Home .miniContact .textArea {
  display: block;
  width: 60%;
  margin: 0 auto;
  padding-right: 15%;
}

.Home .miniContact textarea {
  color: black;
  width: 100%;
  height: 100px;
  margin-top: 50px;
  font-size: 1.5rem;
  font-weight: 300;
  font-family: "Raleway", sans-serif;
}

.Home .miniContact input[type="submit"] {
  position: relative;
  cursor: pointer;
  padding: 15px 30px;
  color: white;
  background: rgb(29, 32, 43);
  border: 0;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 1.5rem;
  font-family: "Rubik", sans-serif;
  margin-top: 20px;
  transition: 0.4s;
}

.Home .miniContact input[type="submit"]:hover {
  background: rgb(51, 57, 77);
}
.Home .questions {
  min-height: 60vh;
  width: 60%;
  margin: 0 auto;
  text-align: left;
  margin-top: 10vh;
}
.Home .questions > h1 {
  color: rgb(12, 12, 12);
  text-transform: uppercase;
  font-weight: 900;
  font-size: 2rem;
  font-family: "Rubik", sans-serif;
  line-height: 70px;
}

.Home .questions ul {
  list-style-type: none;
}
.Home .questions ul li {
  position: relative;
  animation: fade 2s;
}
.Home .questions ul li .question {
  display: flex;
  cursor: pointer;
  justify-content: space-between;
  margin: 10px 0;
  width: 70%;
  min-width: 40vh;
  transition: 0.4s;
}
.Home .questions ul li::after {
  content: "";
  position: absolute;
  background-color: rgb(201, 201, 201);
  width: 70%;
  height: 2px;
  bottom: -5px;
  transition: 0.4s;
}
.Home .questions ul li .question > h1 {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 1.2rem;
  font-family: "Rubik", sans-serif;
  margin-top: 20px;
}
.Home .questions ul li .question span {
  font-size: 2rem;
  color: rgb(36, 62, 102);
}

.Home .questions ul li p {
  overflow: hidden;
  padding: 10px 0;
  color: rgb(34, 32, 32);
  font-weight: 100;
  font-size: 1.2rem;
  font-family: "Raleway", sans-serif;
  transition: 0.4s;
  animation: fadeIn 2s;
}

.Home .testimonials {
  position: relative;
  margin-top: 5vh;
  background-color: #516437;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-bottom: 10vh;
  margin-bottom: 15vh;
}

.Home .testimonials .leftCorner {
  height: 20vh;
  width: 40%;
  background-color: rgb(255, 255, 255);
  clip-path: polygon(100% 0, 0 31%, 0 0);
}

.Home .testimonials .leftCorner2 {
  top: 0;
  position: absolute;
  height: 20vh;
  width: 100%;
  background-color: #819464;
  clip-path: polygon(35% 0, 75% 0%, 100% 0, 100% 10%, 37% 8%, 0 30%);
}

.Home .testimonials .rightCorner {
  bottom: 0;
  position: absolute;
  height: 20vh;
  width: 100%;
  background-color: #819464;
  clip-path: polygon(82% 90%, 100% 79%, 100% 100%, 65% 100%);
}

.Home .testimonials > h1 {
  color: white;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 3.5rem;
  font-family: "Rubik", sans-serif;
  line-height: 70px;
  width: 60%;
  text-align: left;
  margin: 0 auto;
  animation: fadeInLeft 1s;
}

.Home .testimonials ul {
  display: flex;
  width: 70%;
  min-width: 900px;
  margin: 0 auto;
  margin-top: 100px;
  justify-content: space-around;
  list-style-type: none;
}

.Home .testimonials ul li {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 30%;
  padding: 40px 20px;
  color: white;
  border: 1px solid rgb(158, 155, 155);
  animation: fadeInLeft 1.5s;
}

.Home .testimonials ul li p {
  font-weight: 100;
  font-size: 1.2rem;
  font-family: "Raleway", sans-serif;
}

.Home .testimonials ul li h1 {
  margin-top: 2vh;
  font-weight: 900;
  font-size: 1.2rem;
  font-family: "Rubik", sans-serif;
}

.Home .testimonials ul li .Stars {
  color: rgb(230, 201, 41);
}

.Home .reasons {
  min-height: 80vh;
  width: 60%;
  margin: 0 auto;
  text-align: left;
  margin-top: 10vh;
}
.Home .reasons > h1 {
  color: rgb(12, 12, 12);
  text-transform: uppercase;
  font-weight: 100;
  font-size: 2rem;
  font-family: "Rubik", sans-serif;
}

.Home .reasons ul {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-column-gap: 100px;
  grid-row-gap: 50px;
  grid-auto-rows: 1fr;
  list-style-type: none;
  margin-top: 15vh;
}

.Home .reasons ul li {
  animation: fade 1s;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Home .reasons ul li img {
  width: 40%;
}
.Home .reasons ul li h2 {
  text-transform: uppercase;
  margin-top: 10px;
  font-weight: 900;
  font-size: 1rem;
  font-family: "Rubik", sans-serif;
}

.Home .reasons ul li p {
  color: rgb(34, 32, 32);
  font-weight: 100;
  font-size: 1.2rem;
  font-family: "Raleway", sans-serif;
  line-height: 25px;
  margin-top: 15px;
  animation: fadeInLeft 2s;
}

@keyframes fadeInLeft {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

.footer {
  position: relative;
  background: #1f3e4e;
  min-height: 30vh;
  padding-bottom: 5vh;
  margin-top: 5vh;
}

.footer .container {
  background-color: white;
  width: 60%;
  min-height: 20vh;
  margin: 0 auto;
  padding-bottom: 20px;
}

.footer .container ul {
  background-color: #222425;
  list-style-type: none;
  display: flex;
  width: 100%;
  padding: 10px;
  justify-content: center;
}

.footer .container ul li {
  color: white;
  cursor: pointer;
  padding: 5px 15px;
  font-size: 1.5rem;
  border: 1px solid white;
  border-radius: 10px;
  margin: 0 30px;
  transition: 0.4s;
}
.footer .container ul li:hover {
  color: rgb(153, 151, 151);
  border: 1px solid rgb(153, 151, 151);
}

.footer .container > p {
  width: 60%;
  margin: 0 auto;
  margin-top: 30px;
  font-family: "Raleway", sans-serif;
  line-height: 25px;
  text-align: left !important;
}

.footer .container .details {
  width: 60%;
  margin: 0 auto;
  margin-top: 20px;
}

.footer .container .details > p {
  margin-top: 10px;
  font-size: 1rem;
  text-align: left !important;
  color: rgb(54, 54, 54);
}

.footer .container .details i {
  margin-right: 10px;
}

@media screen and (max-width: 1000px) {
  .Home .HomeHeader h1 {
    font-size: 2.5rem;
    width: 80%;
  }
  .Home .HomeHeader h2 {
    font-size: 1.5rem;
    width: 80%;
  }
  .Home .HomeDescription {
    padding: 15px 25px;
  }

  .Home .HomeDescription h1 {
    font-size: 2rem;
    margin-top: 15px;
  }

  .Home .HomeDescription h2 {
    font-size: 1rem;
    margin-top: 20px;
  }

  .Home .miniContact h1 {
    font-size: 2rem;
    width: 100%;
    padding: 10px;

    line-height: 30px;
  }

  .Home .miniContact h2 {
    width: 95%;
    margin-top: 10px;
    font-size: 1rem;
    line-height: 20px;
    padding-left: 15px;
  }

  .Home .miniContact .inputText {
    width: 100%;
    padding-left: 10px;
  }

  .Home .miniContact .textArea {
    width: 100%;
    padding-left: 10px;
  }

  .Home .questions {
    width: 95%;
  }

  .Home .questions > h1 {
    font-size: 1.5rem;
    line-height: 25px;
  }

  .Home .testimonials ul {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    margin-left: 15vh;
  }

  .Home .testimonials ul li {
    margin-top: 10px;
  }

  .Home .testimonials h1 {
    font-size: 2.5rem;
    width: 80%;
  }

  .Home .footer .container {
    width: 95%;
    margin-top: 5vh;
  }
}
